import axios from 'axios'

function axiosCall(method,url,data = {}){
    return axios.request({
        baseURL:'http://65.0.249.156:3000',
        url,
        method,
        data,
        headers:{'Content-Type':'application/json', Authorization:'' }
    }).then(res=>{
        return res;
    }).catch(res=>{
        return res;
    })
}


export function get(url,data = {}){
    return axiosCall('GET',url,data);
}

export function post(url,data = {}){
    return axiosCall('POST',url,data);
}

export function put(url,data = {}){
    return axiosCall('GET',url,data);
}

export function del(url,data = {}){
    return axiosCall('DELETE',url,data);
}

export default function custom(method,url,data = {}){
    return axiosCall(method,url,data)
}


