<template>
    <div>
        <button class="btn btn-danger" @click="click" v-bind:data-id=id>Delete</button>
    </div>
</template>
<style scoped>

</style>
<script>
import { del } from '../helpers/api'
import swal from 'sweetalert';
export default {
    name:"ConfirmDeleteButton",
    props:{ id:String,url: String ,title:String,subtext:{default:"",type:String},data:{type: Object},callback:Function},
    methods:{
         click(){
           swal({
                title: this.title,
                text: this.subtext,
                icon: "warning",
                buttons: true,
            })
            .then(async (willDelete) => {
                if (willDelete) {
                    console.log(this.data);
                    //console.log(JSON.parse(this.data));
                    await del(this.url,this.data).then(()=>{
                        swal("Deleted Successfuly", {
                            icon: "success",
                         }).then(()=>{
                             this.callback();
                         });
                    })
                   
                }
            });
        }
    }
}
</script>